.App {
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
  padding: 20px;
}

.scoreboard {
  display: flex;
  justify-content: center;
  gap: 50px;
  margin: 40px 0;
}

.player {
  border: 2px solid #4caf50;
  border-radius: 12px;
  padding: 20px;
  width: 180px;
  background-color: #f0fff0;
}

.player h3 {
  font-size: 2.5em;
  margin: 10px 0;
}

.player button {
  margin: 5px;
  padding: 8px 16px;
  font-size: 1em;
  cursor: pointer;
}

.result {
  margin: 20px;
  font-size: 1.5em;
  color: #d32f2f;
}

.reset, .restart {
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.reset {
  background-color: #2196f3;
  color: white;
}

.restart {
  background-color: #f44336;
  color: white;
  margin-top: 10px;
}

.player-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-top: 30px;
}

.player-form input {
  padding: 10px;
  width: 250px;
  font-size: 1em;
  border: 1px solid #aaa;
  border-radius: 6px;
}

.player-form button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.game-type {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}
